import React, { Component } from 'react';

import logo from '../img/which-investment-account.png';

export default class Navbar extends Component {

  render(props) {
    return (
      <div className="nav container">
        <div className="row">
          <div className="col-xs-12">
            <a onClick={this.props.displayHome}>
              <img className="logo center-block" src={logo} alt="wia logo"/>
            </a>
          </div>
        </div>
      </div>
    );
  }
}



