import React, { Component } from 'react';

export default class College extends Component {

  render(props) {
      return (

          <div>
            <h3>Will these funds be used ONLY for higher education expenses?</h3>
            <br/>
            <div className="btn-group" data-toggle="buttons">
              <label className="btn btn-success btn-lg">
                <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displayFiveTwentyNineAccount}/> Yes
              </label>
              <label className="btn btn-info btn-lg">
                <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayUtmaAccount}/> No
              </label>
            </div>
          </div>
      );
  }
}



