import React, { Component } from 'react';

import College from './College.js';

export default class Children extends Component {

  render(props) {
    if (this.props.children === true) {
      return (
          <div>
            <College
              displayUtmaAccount={this.props.displayUtmaAccount}
              displayFiveTwentyNineAccount={this.props.displayFiveTwentyNineAccount}
            />
          </div>
      );
    }
    else {
      return null;
    }
  }
}



