import React, { Component } from 'react';

export default class taxPreference extends Component {

  render(props) {
    if (this.props.taxPreference === true) {
      return (
          <div>
            <h3>How would you like approach taxes in your retirement account?</h3>
            <br/>
            <div className="btn-group" data-toggle="buttons">
              <label className="btn btn-success btn-lg btn-block">
                <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displayRothIraAccount}/> Pay taxes now/withdraw money tax <br/>free in retirement (suggested)
              </label>
              <label className="btn btn-info btn-lg btn-block">
                <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayIraAccount}/> Receive tax benefits now/pay taxes <br/>on withdrawals in retirement
              </label>
            </div>
          </div>
      );
    }
    else {
      return null;
    }
  }
}



