import React, { Component } from 'react';

import AllyTextAd from './AllyTextAd.js';
import MobileAllyAd from './MobileAllyAd.js';
import DesktopAllyAd from './DesktopAllyAd.js';

export default class JointAccount extends Component {
  render(props) {
    if (this.props.showJoint === true) {
      return (
          <div id="account" className="container">
            <div className="row">
              <div className="col-xs-12">
                <h2>Your Recommended Investment Account:</h2>
                <h2 className="name">Joint Account</h2>
                <p>Joint investment accounts are great options for spouses or family members looking to share ownership of an account. Joint investment accounts are very similar to joint bank accounts except that the money within the account is invested and your funds are NOT insured by the FDIC. Both owners of the joint account enjoy the right to control 100% of the funds inside the account. All money put in a joint account must be after-tax dollars and there is no special tax treatment for funds inside of the account. You must pay ordinary income tax on all stock dividends and fund distributions earned within the account. Any money earned from the buy and sale of a stock within 1 year will also be subject to your ordinary income tax rate. Investments that are sold after being held for longer than 1 year will be taxed at the lower <a href="https://www.fool.com/retirement/2016/12/11/long-term-capital-gains-tax-rates-in-2017.aspx" target="_blank" rel="noopener noreferrer">capital gains rate</a>. Funds inside of a joint account may be withdrawn at any time by either owner without any penalty, but some brokerages have a 3 day waiting period for account withdrawals. There is no contribution limit for joint accounts and funds may be kept in the account for as long as either owner deires.  </p>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <MobileAllyAd />
                <DesktopAllyAd />
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Tax Treatment</h4>
                <p>There is no special tax treatment for joint investment accounts. You must pay ordinary income tax on all stock dividends and fund distributions earned within the account. Any investment that is bought and sold within 1 year will be subject to your ordinary income tax rate as well. Investments that are sold after being held for over 1 year will be taxed at the lower <a href="https://www.fool.com/retirement/2016/12/11/long-term-capital-gains-tax-rates-in-2017.aspx" target="_blank" rel="noopener noreferrer">capital gains tax rate</a>. Most Americans will pay either a 0% or 15% capital gains tax rate, but members of the highest tax bracket will pay a 20% capital gains tax. </p>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Advantages</h4>
                <ul>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Easy transfer of assets in the event of one owner passing away
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Funds can be withdrawn at any time by either owner
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> No limits on the amount of money contributed
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Investment options are unlimited
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Disadvantages</h4>
                <ul>
                  <li>
                    <i className="fa fa-minus-circle text-danger"></i> Trust is absolutely crucial between both account owners
                   </li>
                  <li>
                    <i className="fa fa-minus-circle text-danger"></i> All gains within the account will be taxed
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <AllyTextAd
                  ad="Open a Joint Investment Account"
                />
              </div>
            </div>
          </div>
      );
    } else {
      return null;
    }
  }
}



