import React, { Component } from 'react';

import AllyTextAd from './AllyTextAd.js';
import MobileAllyAd from './MobileAllyAd.js';
import DesktopAllyAd from './DesktopAllyAd.js';

export default class FiveTwentyNineAccount extends Component {
  render(props) {
    if (this.props.showFiveTwentyNine === true) {
      return (
          <div id="account" className="container">
            <div className="row">
              <div className="col-xs-12">
                <h2>Your Recommended Investment Account:</h2>
                <h2 className="name">529 Plan</h2>
                <p>If you're looking for a way to save/invest for higher-education expenses, then the <a href="http://www.savingforcollege.com/intro_to_529s/what-is-a-529-plan.php" target="_blank" rel="noopener noreferrer">529 Plan</a> is a great choice. The 529 allows investors to contribute after-tax earnings and enjoy tax-free growth with tax-free withdrawals so long as the withdrawn money is used for higher-education. Unlike UTMA Custodial Accounts, money deposited into a 529 ALWAYS remains under control of the account owner and does not have to be transferred to the beneficiary upon the age of majority. In addition to the federal tax benefits on growth within the 529 account, many states offer state tax benefits for contributing to 529 Plans. Investment options within the 529 Plan will vary depending on the state in which you reside. Unfortunately, withdrawals from a 529 can ONLY be used for higher-education expenses such as tuition, fees, books, and housing costs (as long as housing costs do not exceed the cost of on-campus housing). Withdrawals not used for higher-education expenses will subject to ordinary federal income tax AND a 10% penalty.  </p>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <MobileAllyAd />
                <DesktopAllyAd />
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Tax Treatment</h4>
                <p>Only after-tax dollars can be contributed to a 529 Plan (meaning you won't be able to deduct contributions). Funds inside of a 529 Account grow tax-free and can be withdrawn tax-free as long as withdrawals are used for qualifying higher-education expenses. Withdrawals used for non-qualifying expenses will be subject to ordinary federal income tax and a 10% penalty.</p>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Advantages</h4>
                <ul>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Savings grow tax-free within the account
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Withdrawals are tax-free when used for higher-education expenses
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Account owner always controls funds within the account
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Disadvantages</h4>
                <ul>
                  <li>
                    <i className="fa fa-minus-circle text-danger"></i> Withdrawals can ONLY be used for higher-education expenses
                   </li>
                  <li>
                    <i className="fa fa-minus-circle text-danger"></i> Investment options can be slightly limited
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <AllyTextAd
                  ad="Open an Investment Account"
                />
              </div>
            </div>
          </div>
      );
    } else {
      return null;
    }
  }
}



