import React, { Component } from 'react';

import Joint from './Joint.js';

export default class Savings extends Component {

  render(props) {
    if (this.props.savings === true) {
      return (
          <div>
            <Joint
              displayIndividualAccount={this.props.displayIndividualAccount}
              displayJointAccount={this.props.displayJointAccount}
            />
          </div>
      );
    }
    else {
      return null;
    }
  }
}



