import React, { Component } from 'react';

export default class MaritalStatus extends Component {

  render(props) {
    if (this.props.maritalStatus === true) {
      return (
          <div>
            <h3>Are you single or married?</h3>
            <br/>
            <div className="btn-group" data-toggle="buttons">
              <label className="btn btn-success btn-lg">
                <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displaySingle}/> Single
              </label>
              <label className="btn btn-info btn-lg">
                <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayMarried}/> Married
              </label>
            </div>
          </div>
      );
    }
    else {
      return null;
    }
  }
}



