import React, { Component } from 'react';

export default class Contact extends Component {
  render(props) {
    if (this.props.showContact === true) {
      return (
          <div id="contact" className="container">
            <div className="row">
              <div className="col-xs-12">
                <h3>Contact</h3>
                <p>Email: <a href="mailto:gstephe7@gmail.com" target="_blank" rel="noopener noreferrer">gstephe7@gmail.com</a></p>
              </div>
            </div>
          </div>
      );
    } else {
      return null;
    }
  }
}



