import React, { Component } from 'react';

import Children from './children/Children.js';
import Retirement from './retirement/Retirement.js';
import Savings from './savings/Savings.js';

export default class Calculator extends Component {
  render(props) {
    if (this.props.showHome === true) {
      return (
        <div className="calculator container">
          <div className="row">
            <div className="text-center center-block col-xs-12">
              <h3>What are you saving/investing for?</h3>
              <br/>
              <div className="btn-group visible-xs" data-toggle="buttons">
                <label className="btn btn-primary btn-block btn-lg">
                  <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displayRetirement}/> Retirement
                </label>
                <br/>
                <label className="btn btn-warning btn-block btn-lg">
                  <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayChildren}/> Children's Future
                </label>
                <br/>
                <label className="btn btn-danger btn-block btn-lg">
                  <input type="radio" name="options" id="option3" autoComplete="off" onClick={this.props.displaySavings}/> General Savings
                </label>
              </div>
              <div className="btn-group hidden-xs" data-toggle="buttons">
                <label className="btn btn-primary btn-lg">
                  <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displayRetirement}/> Retirement
                </label>
                <label className="btn btn-warning btn-lg">
                  <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayChildren}/> Children's Future
                </label>
                <label className="btn btn-danger btn-lg">
                  <input type="radio" name="options" id="option3" autoComplete="off" onClick={this.props.displaySavings}/> General Savings
                </label>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="text-center center-block col-xs-12">
              <hr/>
              <Retirement
                retirement={this.props.retirement}
                displayFourOhOneKAccount={this.props.displayFourOhOneKAccount}
                showFourOhOneK={this.props.showFourOhOneK}
                displayIraAccount={this.props.displayIraAccount}
                displayRothIraAccount={this.props.displayRothIraAccount}
              />
              <Children
                children={this.props.children}
                displayUtmaAccount={this.props.displayUtmaAccount}
                displayFiveTwentyNineAccount={this.props.displayFiveTwentyNineAccount}
              />
              <Savings
                savings={this.props.savings}
                displayJointAccount={this.props.displayJointAccount}
                displayIndividualAccount={this.props.displayIndividualAccount}
              />
            </div>
          </div>
        </div>
      );
    }
    else {
      return null;
    }
  }
}



