import React, { Component } from 'react';

export default class Married extends Component {

  render(props) {
    if (this.props.married === true) {
      return (
          <div>
            <h3>Does your family earn more than $195,999 a year?</h3>
            <br/>
            <div className="btn-group" data-toggle="buttons">
              <label className="btn btn-success btn-lg">
                <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displayIraAccount}/> Yes
              </label>
              <label className="btn btn-info btn-lg">
                <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayTaxPreference}/> No
              </label>
            </div>
          </div>
      );
    }
    else {
      return null;
    }
  }
}



