import React, { Component } from 'react';
import 'bootstrap/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.min.css';
import './App.css';

import Calculator from './components/Calculator.js';
import Contact from './components/Contact.js';
import FiveTwentyNineAccount from './components/FiveTwentyNineAccount.js';
import FourOhOneKAccount from './components/FourOhOneKAccount.js';
import GettingStarted from './components/GettingStarted.js';
import IndividualAccount from './components/IndividualAccount.js';
import IraAccount from './components/IraAccount.js';
import JointAccount from './components/JointAccount.js';
import MobileGoogleAd from './components/MobileGoogleAd.js';
import Navbar from './components/Navbar.js';
import Footer from './components/Footer.js';
import Privacy from './components/Privacy.js';
import RothIraAccount from './components/RothIraAccount.js';
import UtmaAccount from './components/UtmaAccount.js';
import VerticalGoogleAd from './components/VerticalGoogleAd.js';

class App extends Component {
  constructor() {
    super();
    this.state = {
      showHome: true,
      showContact: false,
      showPrivacy: false,
      retirement: false,
      children: false,
      savings: false,
      showFourOhOneK: false,
      showIra: false,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: false
    }
    this.displayHome = this.displayHome.bind(this);
    this.displayContact = this.displayContact.bind(this);
    this.displayPrivacy = this.displayPrivacy.bind(this);
    this.displayRetirement = this.displayRetirement.bind(this);
    this.displayChildren = this.displayChildren.bind(this);
    this.displaySavings = this.displaySavings.bind(this);
    this.displayFourOhOneKAccount = this.displayFourOhOneKAccount.bind(this);
    this.displayIraAccount = this.displayIraAccount.bind(this);
    this.displayRothIraAccount = this.displayRothIraAccount.bind(this);
    this.displayFiveTwentyNineAccount = this.displayFiveTwentyNineAccount.bind(this);
    this.displayUtmaAccount = this.displayUtmaAccount.bind(this);
    this.displayIndividualAccount = this.displayIndividualAccount.bind(this);
    this.displayJointAccount = this.displayJointAccount.bind(this);
  }

  displayHome() {
    this.setState({
      showHome: true,
      showContact: false,
      showPrivacy: false,
      retirement: false,
      children: false,
      savings: false,
      showFourOhOneK: false,
      showIra: false,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: false
    });
    setTimeout(function() {
      var element = document.getElementById('home');
      element.scrollIntoView();
    }, 10);
  }

  displayContact() {
    this.setState({
      showHome: false,
      showContact: true,
      showPrivacy: false
    })
  }

  displayPrivacy() {
    this.setState({
      showHome: false,
      showContact: false,
      showPrivacy: true
    })
  }

  displayRetirement() {
    this.setState({
      retirement: true,
      children: false,
      savings: false
    });
    setTimeout(function() {
      var element = document.getElementById('calc');
      element.scrollIntoView();
    }, 10);
  }

  displayChildren() {
    this.setState({
      retirement: false,
      children: true,
      savings: false
    });
    setTimeout(function() {
      var element = document.getElementById('calc');
      element.scrollIntoView();
    }, 10);
  }

  displaySavings() {
    this.setState({
      retirement: false,
      children: false,
      savings: true
    });
    setTimeout(function() {
      var element = document.getElementById('calc');
      element.scrollIntoView();
    }, 10);
  }

  displayFourOhOneKAccount() {
    this.setState({
      showFourOhOneK: true,
      showIra: false,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: false
    });
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  displayIraAccount() {
    this.setState({
      showFourOhOneK: false,
      showIra: true,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: false
    })
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  displayRothIraAccount() {
    this.setState({
      showFourOhOneK: false,
      showIra: false,
      showRothIra: true,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: false
    })
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  displayFiveTwentyNineAccount() {
    this.setState({
      showFourOhOneK: false,
      showIra: false,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: true,
      showJoint: false,
      showIndividual: false
    })
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  displayUtmaAccount() {
    this.setState({
      showFourOhOneK: false,
      showIra: false,
      showRothIra: false,
      showUtma: true,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: false
    })
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  displayIndividualAccount() {
    this.setState({
      showFourOhOneK: false,
      showIra: false,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: false,
      showIndividual: true
    })
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  displayJointAccount() {
    this.setState({
      showFourOhOneK: false,
      showIra: false,
      showRothIra: false,
      showUtma: false,
      showFiveTwentyNine: false,
      showJoint: true,
      showIndividual: false
    })
    setTimeout(function() {
      var element = document.getElementById('account');
      element.scrollIntoView();
    }, 10);
  }

  render() {
    return (
      <div className="container">
        <Navbar
          displayHome={this.displayHome}
        />

        <div className="container">
          <div className="row">
            <div className="col-sm-2 hidden-xs">
              <VerticalGoogleAd />*
            </div>

            <div className="col-sm-8 col-xs-12">
              <GettingStarted
                showHome={this.state.showHome}
              />
              <Calculator
                showHome={this.state.showHome}
                displayRetirement={this.displayRetirement}
                displayChildren={this.displayChildren}
                displaySavings={this.displaySavings}
                displayFourOhOneKAccount={this.displayFourOhOneKAccount}
                displayIraAccount={this.displayIraAccount}
                displayRothIraAccount={this.displayRothIraAccount}
                displayUtmaAccount={this.displayUtmaAccount}
                displayFiveTwentyNineAccount={this.displayFiveTwentyNineAccount}
                displayIndividualAccount={this.displayIndividualAccount}
                displayJointAccount={this.displayJointAccount}
                retirement={this.state.retirement}
                children={this.state.children}
                savings={this.state.savings}
                showFourOhOneK={this.state.showFourOhOneK}
              />
              <MobileGoogleAd />
              <FourOhOneKAccount
                showFourOhOneK={this.state.showFourOhOneK}
              />
              <IraAccount
                showIra={this.state.showIra}
              />
              <RothIraAccount
                showRothIra={this.state.showRothIra}
              />
              <FiveTwentyNineAccount
                showFiveTwentyNine={this.state.showFiveTwentyNine}
              />
              <UtmaAccount
                showUtma={this.state.showUtma}
              />
              <IndividualAccount
                showIndividual={this.state.showIndividual}
              />
              <JointAccount
                showJoint={this.state.showJoint}
              />
              <Privacy
                showPrivacy={this.state.showPrivacy}
              />
              <Contact
                showContact={this.state.showContact}
              />
            </div>

            <div className="col-sm-2 hidden-xs">
              <VerticalGoogleAd />
            </div>
          </div>
        </div>

        <Footer
          displayHome={this.displayHome}
          displayContact={this.displayContact}
          displayPrivacy={this.displayPrivacy}
        />

      </div>
    );
  }
}

export default App;



