import React, { Component } from 'react';

import AllyTextAd from './AllyTextAd.js';
import MobileAllyAd from './MobileAllyAd.js';
import DesktopAllyAd from './DesktopAllyAd.js';

export default class UtmaAccount extends Component {
  render(props) {
    if (this.props.showUtma === true) {
      return (
          <div id="account" className="container">
            <div className="row">
              <div className="col-xs-12">
                <h2>Your Recommended Investment Account:</h2>
                <h2 className="name">UTMA (Custodial Account)</h2>
                <p>If you are looking to save for your child's future, but aren't quite sure if the funds will be used for higher-education, then the UTMA/custodial account is the best option for you. In a custodial account, the custodian (usually the parent) manages the account until the beneficiary (usually the child) becomes the age of majority. Definition of the "age of majority" varies by state but will typically be either 18, 19, or 21. Once the beneficiary becomes the age of majority, he/she receives full ownership of the custodial account and will have complete control of all funds within the account. Unlike the 529 Plan, the beneficiary can use withdrawals from a custodial account for anything. Contributions made to a custodial account must be after-tax dollars (meaning you cannot deduct contributions). Anyone can contribute up to $14,000/year to the custodial account without running into the gift tax and most earnings within the account will be taxed at the child's tax rate (usually lower than the parents' tax rate). Funds within the account <em>can</em> be withdrawn before the child reaches the age of majority, but the funds must be used for the benefit of the child (costs of education, a car, summer camp, etc...). It is important to note that funds within the account will count against the child when applying for college financial aid.  </p>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <MobileAllyAd />
                <DesktopAllyAd />
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Tax Treatment</h4>
                <p>Contributions to a custodial account must be after-tax dollars (meaning that contributions cannot be deducted). All annual earnings within the account less than $1,050 will be tax-free. Annual earnings within the account between $1,050 and $2,100 will be taxed at the child's tax rate, and earnings over $2,100 will be taxed at the parent's tax rate. Funds withdrawn by the account beneficiary after reaching the age of majority will be tax-free. </p>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Advantages</h4>
                <ul>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Funds can be used by the beneficiary for anything (not just education)
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Most gains within the account are tax-free or taxed at a lower tax rate
                  </li>
                  <li>
                    <i className="fa fa-plus-circle text-success"></i> Withdrawals are tax-free for the beneficiary
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <h4>Disadvantages</h4>
                <ul>
                  <li>
                    <i className="fa fa-minus-circle text-danger"></i> Full ownership and control of account is transferred to beneficiary at 18 or 21
                   </li>
                  <li>
                    <i className="fa fa-minus-circle text-danger"></i> Funds can count against student financial aid for beneficiary
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <AllyTextAd
                  ad="Open a Custodial Account"
                />
              </div>
            </div>
          </div>
      );
    } else {
      return null;
    }
  }
}



