import React, { Component } from 'react';

import Employer from './Employer.js'
import FourOhOneK from './FourOhOneK.js';
import MaritalStatus from './MaritalStatus.js';
import Married from './Married.js';
import Single from './Single.js';
import TaxPreference from './TaxPreference.js';

export default class Retirement extends Component {
  constructor() {
    super();
    this.state = {
      employer: true,
      married: false,
      single: false,
      fourOhOneK: false,
      maritalStatus: false,
      taxPreference: false
    }
    this.displayMarried = this.displayMarried.bind(this);
    this.displaySingle = this.displaySingle.bind(this);
    this.displayFourOhOneK = this.displayFourOhOneK.bind(this);
    this.displayMaritalStatus = this.displayMaritalStatus.bind(this);
    this.displayTaxPreference = this.displayTaxPreference.bind(this);
    this.displayEmployer = this.displayEmployer.bind(this);
  }

  displayEmployer() {
    this.setState({
      employer: true,
      married: false,
      single: false,
      fourOhOneK: false,
      maritalStatus: false,
      taxPreference: false
    })
  }

  displayFourOhOneK() {
    this.setState({
      employer: false,
      married: false,
      single: false,
      fourOhOneK: true,
      maritalStatus: false,
      taxPreference: false
    })
  }

  displayMaritalStatus() {
    this.setState({
      employer: false,
      married: false,
      single: false,
      fourOhOneK: false,
      maritalStatus: true,
      taxPreference: false
    })
  }

  displayMarried() {
    this.setState({
      employer: false,
      married: true,
      single: false,
      fourOhOneK: false,
      maritalStatus: false,
      taxPreference: false
    })
  }

  displaySingle() {
    this.setState({
      employer: false,
      married: false,
      single: true,
      fourOhOneK: false,
      maritalStatus: false,
      taxPreference: false
    })
  }

  displayTaxPreference() {
    this.setState({
      employer: false,
      married: false,
      single: false,
      fourOhOneK: false,
      maritalStatus: false,
      taxPreference: true
    })
  }

  render(props) {
    if (this.props.retirement === true) {
      return (
        <div>
          <Employer
            employer={this.state.employer}
            displayFourOhOneK={this.displayFourOhOneK}
            displayMaritalStatus={this.displayMaritalStatus}
          />
          <FourOhOneK
            fourOhOneK={this.state.fourOhOneK}
            displayFourOhOneKAccount={this.props.displayFourOhOneKAccount}
            showFourOhOneK={this.props.showFourOhOneK}
            displayMaritalStatus={this.displayMaritalStatus}
          />
          <MaritalStatus
            displayMarried={this.displayMarried}
            displaySingle={this.displaySingle}
            maritalStatus={this.state.maritalStatus}
          />
          <Married
            married={this.state.married}
            displayTaxPreference={this.displayTaxPreference}
            displayIraAccount={this.props.displayIraAccount}
          />
          <Single
            single={this.state.single}
            displayTaxPreference={this.displayTaxPreference}
            displayIraAccount={this.props.displayIraAccount}
          />
          <TaxPreference
            displayRothIraAccount={this.props.displayRothIraAccount}
            displayIraAccount={this.props.displayIraAccount}
            taxPreference={this.state.taxPreference}
          />
        </div>
      );
    }
    else {
      return null;
    }
  }
}



