import React, { Component } from 'react';

export default class GettingStarted extends Component {

  render(props) {
    if (this.props.showHome === true) {
      return (
        <div id="home" className="getting-started container">
          <div className="row">
            <div className="col-xs-12">
              <h1>Getting Started</h1>
              <p>Deciding which investment account is right for you can be an intimidating task. So WhichInvestmentAccount.com has decided to make that decision easier. Whether you're looking to invest for retirement, your children's future, or just save for a big purchase, our free calculator will fit you with the right investment account for your situation. Answer the questions below to find out which investment account is right for you. </p>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12">
              <h1 id="calc">Find The Right Investment Account</h1>
            </div>
          </div>
        </div>
      );
    }
    else {
      return null;
    }
  }
}



