import React, { Component } from 'react';

export default class Joint extends Component {

  render(props) {
      return (
          <div>
            <h3>Would you like to share ownership of this account with a family member?</h3>
            <br/>
            <div className="btn-group" data-toggle="buttons">
              <label className="btn btn-success btn-lg">
                <input type="radio" name="options" id="option1" autoComplete="off" onClick={this.props.displayJointAccount}/> Yes
              </label>
              <label className="btn btn-info btn-lg">
                <input type="radio" name="options" id="option2" autoComplete="off" onClick={this.props.displayIndividualAccount}/> No
              </label>
            </div>
          </div>
      );
  }
}



